.hero {
	background-color : $cotton-blue;
	display          : flex;
	flex-direction   : column;
	height           : 100vh;
	justify-content  : end;
	padding          : 185px 0 151px 0;
	position         : relative;

	@media screen and (min-width : $break-table) {
		justify-content : center;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--hero {
	.swiper-slide {
		width : 85%;

		@media screen and (min-width : $break-table) {
			width : 60%;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero-item {
	position : relative;
}

.hero-item__title {
	color       : $white;
	font-family : 'Branch';
	font-size   : 50px;
	font-style  : normal;
	font-weight : 400;
	left        : 50%;
	line-height : 54px;
	max-width   : 286px;
	position    : absolute;
	text-align  : center;
	top         : 50%;
	transform   : translate(-50%, -50%);
	width       : 100%;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 100px;
		line-height : 108px;
		max-width   : 580px;
	}
}

.hero-item__img {
	font-size : 0;

	img {

		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.hero__arrow {
	bottom    : 70px;
	left      : 50%;
	position  : absolute;
	transform : translateX(-50%);
}
