.product {
	background-color : $cotton-blue;
}

.product__title {
	color         : $deep-teal;
	margin-bottom : 40px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 53px;
	}
}

.product__text {
	color : $deep-teal;


	@media screen and (min-width : $break-desktop) {
		max-width : 645px;
	}
}
