.booked {
	padding : 30px 0 40px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 88px 0;
	}
}

.booked__wrapper {
	background-color : $cyan;
	border-radius    : 16px;
	grid-row-gap     : 40px;
	padding          : 50px 45px 0 45px;

	@media screen and (min-width : $break-desktop) {
		border-radius : 30px;
		grid-row-gap  : 0;
		padding       : 81px 91px 0 91px;
	}
}

.booked__title {
	color         : $white;
	margin-bottom : 37px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.booked__text {
	color         : $white;
	margin-bottom : 38px;

	@media screen and (min-width : $break-desktop) {
		max-width : 480px;
	}
}

.booked__img {
	grid-column : 4 span;

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/6;
	}

}

.booked__wrap {
	grid-column : 4 span;


	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
	}
}
