.header {
	background-color : transparent;
	left             : 0;
	padding          : 28px 0;
	position         : fixed;
	top              : 0;
	transition       : background-color .3s ease;
	width            : 100%;
	z-index          : 2;

	@media screen and (min-width : $break-table) {
		padding : 15px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}


	.nav__link {
		&:before {
			background-color : $deep-teal;
		}
	}

	&.active {
		background-color : $cotton-blue;

	}

}

.header--secondary {
	.header__logo {
		svg {

			path {
				fill   : $white;
				stroke : $white;
			}
		}
	}

	.nav__link {
		color : $white;

		&.active {
			&:before {
				background-color : $white;
			}
		}
	}

	.b-btn--green {
		background-color : $cotton-blue;
		color            : $deep-teal;
	}

	.burger {
		color : $white;
	}
}

.header--secondary.active {
	.header__logo {
		svg {

			path {
				fill   : $deep-teal;
				stroke : $deep-teal;
			}
		}
	}

	.nav__link {
		color : $deep-teal;

		&.active {
			&:before {
				background-color : $deep-teal;
			}
		}
	}

	.b-btn--green {
		background-color : $deep-teal;
		color            : $white;
	}

	.burger {
		color : $deep-teal;
	}
}

.header--third {
	.header__logo {
		svg {

			path {
				fill   : $white;
				stroke : $white;
			}
		}
	}

	.nav__link {
		color : $white;

		&.active {
			&:before {
				background-color : $white;
			}
		}
	}

	.burger {
		color : $white;
	}
}

.header--third.active {
	.header__logo {
		svg {

			path {
				fill   : $deep-teal;
				stroke : $deep-teal;
			}
		}
	}

	.nav__link {
		color : $deep-teal;

		&.active {
			&:before {
				background-color : $deep-teal;
			}
		}
	}

	.burger {
		color : $deep-teal;
	}

}

.header--active {
	background-color : $cotton-blue;

	.header__logo {
		svg {

			path {
				fill   : $deep-teal;
				stroke : $deep-teal;
			}
		}
	}

	.burger {
		color : $deep-teal;
	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;


}

.nav {
	margin-bottom : 85px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.nav__list {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		align-items    : start;
		flex-direction : row;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.nav__item {
	&:not(:last-child) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-table) {
		&:not(:last-child) {
			margin-bottom : 0;
		}
	}

	& + & {
		@media screen and (min-width : $break-table) {
			margin-left : 32px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 68px;
		}
	}


}

.nav__link {
	color          : $deep-teal;
	font-size      : 26px;
	font-weight    : 300;
	letter-spacing : 0.25em;
	line-height    : 100.5%;
	position       : relative;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size      : 14px;
		letter-spacing : 0.145em;
		line-height    : 16px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-color : $deep-teal;
		bottom           : -9px;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
	}


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size      : 16px;
		letter-spacing : 0.345em;
		line-height    : 18px;
	}

	&.active {

		@media screen and (min-width : $break-table) {
			&:before {
				width : 100%;
			}
		}

	}

}

.header__logo {
	height : 79px;
	width  : 89px;

	svg {
		path {
			transition : fill .3s ease, stroke .3s ease;
		}
	}

	@media screen and (min-width : $break-desktop) {
		height : 93px;
		width  : 105px;
	}
}

.header__wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	.b-btn--green {
		margin-bottom : 100px;
		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
			margin-left   : 40px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 90px;
		}

	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $deep-teal;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;
	z-index          : 101;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 1px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.header__panel {
	height           : 100svh;
	opacity          : 0;
	padding-top      : 135px;
	position         : absolute;
	right            : 0;
	top              : 0;
	transition       : visibility .3s ease, opacity .3s ease;
	visibility       : hidden;
	width            : 100%;
	z-index          : 100;
	overflow-y: auto;

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		height           : auto;
		opacity          : 1;
		overflow-y       : unset;
		padding-top      : 0;
		position         : unset;
		visibility       : visible;
	}

	&.active {
		opacity    : 1;
		transition : visibility .3s ease, opacity .3s ease;
		visibility : visible;

		.nav__link {
			color : $deep-teal;
		}
	}
}

.header__wrap {
	align-items      : center;
	background-color : #B6D4D6;
	display          : flex;
	flex-direction   : column;
	height           : 100%;
	justify-content  : flex-start;
	padding          : 16px 16px 40px;

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		flex-direction   : row;
		justify-content  : end;
		padding          : 0;
	}
}

.header__mobile {
	@media screen and (min-width : $break-table) {
		display : none;
	}

	.s-block {
		margin-bottom : 68px;
	}
}

.header__links {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	a {
		color       : $deep-teal;
		display     : block;
		font-size   : 20px;
		font-weight : 300;
		line-height : 141%;
		text-align  : center;
	}
}
