.service-list {
	background-color : $cotton-blue;
	padding          : 63px 0 73px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 135px 0 105px 0;
	}
}

.service-list__head {
	margin-bottom : 30px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 73px;
	}
}

.service-list__title {
	color : $deep-teal;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 63px;
	}
}

.service-list__text {
	color     : $deep-teal;
	display   : none;
	margin    : 0 auto;
	max-width : 596px;
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.service-item {
	width : 100%;

	&:first-child {
		border-bottom : 1px solid $deep-teal;
		border-top    : 1px solid $deep-teal;
		padding       : 20px 0 40px 0;

		@media screen and (min-width : $break-desktop) {
			padding : 50px 0 40px 0;
		}
	}

	&:not(:first-child) {
		padding : 20px 0 20px 0;

		@media screen and (min-width : $break-desktop) {
			padding : 20px 0 20px 0;
		}
	}
}

.service-list__wrap {
	margin    : 0 auto;
	max-width : 829px;
}

.service-list__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	margin-bottom  : 34px;

	@media screen and (min-width : $break-desktop) {
		flex-direction  : row;
		justify-content : space-between;
		margin-bottom   : 16px;
	}
}

.service-item__name {
	color          : $deep-teal;
	font-size      : 26px;
	font-weight    : 300;
	letter-spacing : 0.04em;
	line-height    : 107.5%;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}

}

.service-item__price {
	color          : $deep-teal;
	font-size      : 20px;
	font-weight    : 300;
	letter-spacing : 0.04em;
	line-height    : 107.5%;

	@media screen and (min-width : $break-desktop) {
		font-size : 26px;
	}
}

.service-item__control {
	align-items      : center;
	background-color : transparent;
	border           : none;
	display          : flex;
	padding          : 0;
}

.accordion {
	&__title {

	}

	&__icon {

	}

	&__content {
		box-sizing  : content-box;
		max-height  : 0;
		max-width   : 1000px;
		opacity     : 0;
		overflow    : hidden;
		transition  : all .3s ease-out;
		will-change : max-height;
	}

	&.open {
		.accordion__content {
			opacity     : 1;
			overflow    : visible;
			padding-top : 35px;
		}

		.service-item__icon {
			transform  : rotate(180deg);
			transition : transform .3s ease;
		}
	}
}

.services-item__accordion {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-desktop) {
		align-items : flex-start;
	}
}

.service-item__title {
	color          : $deep-teal;
	font-size      : 20px;
	font-weight    : 500;
	letter-spacing : 0.04em;
	line-height    : 107.5%;
	padding-right  : 4px;
	text-transform : capitalize;

	@media screen and (min-width : $break-desktop) {
		font-weight : 300;
	}

}

.service-item__icon {
	height     : 7px;
	position   : relative;
	top        : 3px;
	transform  : rotate(0deg);
	transition : transform .3s ease;
	width      : 12px;
}

.service-item__text {
	color       : $deep-teal;
	font-size   : 20px;
	font-weight : 300;
	line-height : 107.5%;
	max-width   : 443px;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		text-align : start;
	}
}

