.atmosphere-hero {
	background-color : $cotton-blue;
	padding-top      : 144px;

	@media screen and (min-width : $break-desktop) {
		padding-top : 254px;
	}
}

.atmosphere-hero__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	margin-bottom  : 61px;
	padding        : 0 15px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 90px;
	}
}

.atmosphere-hero__title {
	color         : $deep-teal;
	font-family   : 'Branch';
	font-size     : 40px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 48px;
	margin-bottom : 45px;

	@media screen and (min-width : $break-desktop) {
		font-size     : 100px;
		line-height   : 108px;
		margin-bottom : 65px;
	}
}

.atmosphere-hero__text {
	color         : $deep-teal;
	font-size     : 20px;
	font-weight   : 300;
	line-height   : 120%;
	margin-bottom : 50px;
	max-width     : 870px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {
		font-size     : 26px;
		margin-bottom : 65px;
	}
}

.atmosphere-hero__grid {
	display         : flex;
	justify-content : center;
	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
	}
}

.atmosphere-hero__img {
	flex-shrink : 0;

	@media screen and (min-width : $break-desktop) {
		width : 33.33333%;
	}

	img {
		height : 297px;

		width  : 250px;

		@media screen and (min-width : $break-desktop) {
			height     : 100%;
			object-fit : cover;
			width      : 100%;
		}
	}
}
