.blog-post {
	background-color : $light-nude;
	padding          : 97px 0 161px 0;
}

.blog-post__wrapper {
	margin    : 0 auto;
	max-width : 719px;
}

.blog-post__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 109px;
}

.blog-post__author {
	color          : $deep-teal;
	font-size      : 18px;
	font-weight    : 250;
	letter-spacing : 0.095em;
	line-height    : 148%;
}

.blog-post__date {
	color          : $deep-teal;
	font-size      : 18px;
	font-weight    : 250;
	letter-spacing : 0.095em;
	line-height    : 148%;
}

.blog-post__text {
	color       : $deep-teal;
	font-size   : 26px;
	font-weight : 300;
	line-height : 120%;

	&:not(:last-child) {
		margin-bottom : 85px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 45px;
		}
	}
}

.blog-post__img {
	&:not(:last-child) {
		margin-bottom : 85px;
	}
}
