.hero-section {
	padding : 143px 0 75px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 174px 0 104px 0;
	}
}

.hero-section--services {
	background-color : $deep-teal;
}

.hero-section--blogs {
	background-color : $cyan;
}

.hero-section--post {
	background : $deep-nude;
}

.hero-section__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 60px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.hero-section__title {
	color       : $white;
	font-family : 'Branch';
	font-size   : 50px;
	font-weight : 400;
	grid-column : 4 span;
	line-height : 54px;
	margin      : 0 auto;
	max-width   : 190px;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		font-size   : 100px;
		grid-column : 1/7;
		line-height : 108px;
		max-width   : none;
		text-align  : start;
	}
}

.hero-section__title--blogs {
	max-width : 250px;

	@media screen and (min-width : $break-desktop) {
		max-width : 500px;
	}
}

.hero-section__title--post {
	max-width : 280px;

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}
}

.hero-section__img {
	grid-column : 4 span;
	order       : -1;
	@media screen and (min-width : $break-desktop) {
		grid-column : 8/13;
		order       : 1;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}
