.slider {
	padding : 40px 0 60px 0;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		padding : 109px 0 123px 0;
	}
}

.swiper--slider {

	.swiper-wrapper {
		height : 513px;

		@media screen and (min-width : $break-desktop) {
			height : auto;
		}
	}

	.swiper-slide {

		filter     : blur(0.5px);
		transform  : scale(0.88);
		transition : transform .7s ease;
		width      : 80%;
		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			transform : scale(0.85);
			width     : 75%;
		}

		img {
			border-radius : 21px;
			height        : 100%;
			object-fit    : cover;
			width         : 100%;
		}
	}

	.swiper-slide-active {
		filter     : blur(0);
		transform  : scale(1);
		transition : transform .7s ease;
	}

	.swiper-slide-duplicate {

	}

	.swiper-pagination {
		margin-top : 23px;
		position   : relative;
		z-index    : 3;

		@media screen and (min-width : $break-desktop) {
			margin-top : 45px;
		}
	}

	.swiper-button-prev {
		left : 60px;

	}

	.swiper-button-next {
		right : 60px;
	}

	.swiper-pagination-bullet {
		height : 7px;
		width  : 7px;

		& + & {
			margin-left : 4px;
		}

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			height : 13px;
			width  : 13px;
			& + & {
				margin-left : 8px;
			}
		}
	}
}
