.footer {
	background-color : $cotton-blue;
	padding          : 55px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 65px 0;
	}
}

.footer__grid {
	display               : grid;
	grid-row-gap          : 28px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 51px;
	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 73px;
	}
}

.footer__logo {
	grid-column : 2/4;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/3;
		text-align  : start;
	}
}

.footer__nav {
	grid-column : 2/4;
	text-align  : center;
	@media screen and (min-width : $break-desktop) {
		grid-column : 6/8;
		text-align  : start;
	}

	li {
		&:not(:last-child) {
			margin-bottom : 10px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 20px;
			}
		}

		a {
			color       : #125E5E;
			font-size   : 20px;
			font-weight : 250;
			line-height : 107.5%;

			@media screen and (min-width : $break-desktop) {
				font-size : 26px;
			}
		}
	}
}

.footer__wrap {
	display        : flex;
	flex-direction : column;
	grid-column    : 2/4;

	@media screen and (min-width : $break-desktop) {
		grid-column : 11/13;
	}
}

.footer__links {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	@media screen and (min-width : $break-desktop) {
		align-items   : flex-start;
		margin-bottom : 54px;
	}

	a {
		color       : $deep-teal;
		display     : inline-block;
		font-size   : 20px;
		font-weight : 250;
		line-height : 107.5%;

		@media screen and (min-width : $break-desktop) {
			font-size : 26px;
		}

		&:not(:last-child) {
			margin-bottom : 10px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 18px;
			}
		}
	}
}

.footer__s {
	order : -1;

	@media screen and (min-width : $break-desktop) {
		order : 1;
	}
}

.s-block {
	display         : flex;
	justify-content : center;
	margin-bottom   : 51px;
	order           : -1;
	@media screen and (min-width : $break-desktop) {
		justify-content : flex-start;
		margin-bottom   : 0;
		order           : 1;
	}

	&__link {
		display : block;
		height  : 32px;
		width   : 32px;

		@media screen and (min-width : $break-desktop) {
			height : 39px;
			width  : 39px;
		}

		& + & {
			margin-left : 8px;
		}
	}
}

.footer__copyright {
	text-align : center;
	@media screen and (min-width : $break-desktop) {
		text-align : start;
	}

	a {
		color       : #125E5E;
		font-size   : 16px;
		font-weight : 250;
		line-height : 107.5%;

		@media screen and (min-width : $break-desktop) {
			font-size : 20px;
		}

	}

	span {
		font-size   : 20px;
		line-height : 107.5%;

		@media screen and (min-width : $break-desktop) {
			font-size : 26px;
		}
	}
}
