.questions {
	background-color : $cotton-blue;
	padding          : 90px 0 90px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 100px 0 160px 0;
	}
}

.questions__title {
	color      : $deep-teal;
	margin     : 0 auto 70px auto;
	max-width  : 441px;
	text-align : center;

	@media screen and (min-width : $break-desktop) {
		margin    : 0 auto 126px auto;
		max-width : 441px;
	}
}

.questions__wrapper {
	margin    : 0 auto;
	max-width : 850px;
}

.questions__accordion {
	width : 100%;

	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.questions__control {
	align-items      : center;
	background-color : transparent;
	border           : none;
	border-bottom    : 1px solid #125E5E;
	display          : flex;
	justify-content  : center;
	padding          : 0 0 20px 0;
	width            : 100%;
	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
	}
}

.questions__icon {
	height      : 12px;
	margin-left : 22px;
}

.questions__name {
	color       : $deep-teal;
	font-size   : 20px;
	font-weight : 300;
	line-height : 141%;
	max-width   : 726px;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		font-size   : 30px;
		line-height : 120%;
		max-width   : 726px;
		text-align  : start;
	}
}

.questions__text {
	color       : $deep-teal;
	font-size   : 20px;
	font-weight : 300;
	line-height : 141%;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 120%;
		max-width   : 710px;
		text-align  : start;
	}
}
