.atmosphere {
	background-color : $light-nude;
}

.atmosphere__wrapper {
	grid-row-gap : 80px;
	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 0;
	}
}

.atmosphere__title {
	color         : $deep-teal;
	margin-bottom : 64px;
}

.atmosphere__text {
	color         : $deep-teal;
	margin-bottom : 0;

	@media screen and (min-width : $break-desktop) {
		max-width : 606px;
	}
}



