.travel {
	background-color : $light-nude;
	padding          : 90px 0 70px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 145px 0 175px 0;
	}
}

.travel__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.travel__img {
	grid-column : 4 span;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/7;
		text-align  : start;
	}
}

.travel__wrap {
	grid-column : 4 span;
	order       : -1;

	@media screen and (min-width : $break-desktop) {
		grid-column : 8/13;
		order       : 0;
	}
}

.travel__title {
	color         : $deep-teal;
	margin-bottom : 45px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 20px;
		text-align    : start;
	}
}

.travel__text {
	color       : $deep-teal;
	font-size   : 20px;
	font-weight : 300;
	line-height : 141%;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		font-weight : 300;
		line-height : 120%;
		text-align  : start;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 20px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 40px;
			}
		}
	}
}
