* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : border-box;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	margin             : auto;
	max-width          : 100%;
	padding            : 0 14px;
	width              : (1200 + 30) * 1px;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}


.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}
