.offer {
	background-color : $light-nude;
}

.section-left__wrapper {
	grid-row-gap : 70px;
	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 0;
	}
}

.offer__title {
	color         : $deep-teal;
	margin-bottom : 60px;
}

.offer__text {
	color     : $deep-teal;
	margin    : 0 auto;
	max-width : 622px;
}
