.blogs {
	background-color : $light-nude;
	padding          : 36px 0 86px 0;

	@media screen and (min-width : $break-table) {
		padding : 120px 0 96px 0;
	}
}

.blogs__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 54px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}
}

.blogs__article {
	color          : $deep-teal;
	font-size      : 18px;
	font-weight    : 250;
	letter-spacing : 0.095em;
	line-height    : 148%;
	text-align     : right;
}

.blogs__list {
	display               : grid;
	grid-row-gap          : 86px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 70px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 48px 20px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 145px;
	}

}

.blogs-item {

	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}
}

.blogs-item__img {
	border-radius : 16px;
	margin-bottom : 41px;
	overflow      : hidden;
	padding       : 35px 0 0 30px;
	text-align    : right;

	@media screen and (min-width : $break-table) {
		margin-bottom : 45px;
		padding       : 66px 0 0 60px;
	}
}

.blogs-item--blue {
	.blogs-item__img {
		background-color : $cotton-blue;
	}
}

.blogs-item--green {
	.blogs-item__img {
		background-color : $deep-teal;
	}
}

.blogs-item--cyan {
	.blogs-item__img {
		background-color : $cyan;
	}
}

.blogs-item--brown {
	.blogs-item__img {
		background : $deep-nude;
	}
}

.blogs-item__picture {
	img {
		border-radius : 210px 0 0 0;

		text-align    : right;

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.blogs-item__warp {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		align-items : flex-start;
	}

	.b-btn--green {
		min-width : 160px;

		@media screen and (min-width : $break-table) {
			min-width : 150px;
		}
	}
}

.blogs-item__data {
	color          : $deep-teal;
	font-size      : 18px;
	font-weight    : 250;
	letter-spacing : 0.095em;
	line-height    : 148%;
	margin-bottom  : 33px;
	text-align     : center;

	@media screen and (min-width : $break-table) {
		margin-bottom : 23px;
		text-align    : start;
	}
}

.blogs-item__title {
	color         : $deep-teal;
	font-family   : 'Branch';
	font-size     : 40px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 107.5%;
	margin-bottom : 33px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		text-align : start;
	}
}

.blogs-item__text {
	color         : $deep-teal;
	font-size     : 26px;
	font-weight   : 300;
	line-height   : 120%;
	margin-bottom : 45px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		text-align : start;
	}
}

.blogs__button {
	align-items      : center;
	background-color : transparent;
	border           : none;
	color            : $deep-teal;
	display          : flex;
	font-size        : 26px;
	font-weight      : 300;
	left             : 50%;
	line-height      : 120%;
	padding          : 0;
	position         : relative;
	transform        : translateX(-50%);

	&:after {
		background-image  : url("../images/icon/chevron-right.svg");
		background-repeat : no-repeat;
		content           : "";
		display           : inline-block;
		height            : 14px;
		margin-left       : 12px;
		position          : relative;
		top               : 2px;
		width             : 9px;
	}
}
