.testimonials {
	background-color : $light-nude;
	padding          : 81px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 102px 0;
	}
}

.testimonials__title {
	color         : $deep-teal;
	margin-bottom : 80px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {

	}
}

.testimonials__grid {
	display               : grid;
	grid-row-gap          : 60px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.testimonials-item {
	grid-column : 4 span;

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.testimonials-item__rating {
	display         : flex;
	justify-content : center;
	margin-bottom   : 17px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 22px;
	}

	li {
		&:not(:first-child) {
			margin-left : 5px;

			@media screen and (min-width : $break-desktop) {
				margin-left : 6px;
			}
		}

		img {
			height : 26px;
			width  : 26px;
			@media screen and (min-width : $break-desktop) {
				height : 33px;
				width  : 33px;
			}
		}
	}
}

.testimonials-item__title {
	color         : $deep-teal;
	font-family   : 'Branch';
	font-size     : 30px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 107.5%;
	margin-bottom : 27px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		margin-bottom : 34px;
	}
}

.testimonials-item__text {
	color       : $deep-teal;
	font-size   : 20px;
	font-weight : 250;
	line-height : 107.5%;
	margin      : 0 auto 40px auto;
	max-width   : 416px;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		font-size : 26px;
		margin    : 0 auto 27px auto;
	}
}

.testimonials-item__name {
	color       : $deep-teal;
	font-size   : 26px;
	font-weight : 300;
	line-height : 107.5%;
	text-align  : center;
}
