.mission {
	background-color : $light-nude;
	padding          : 88px 0 66px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 133px 0 139px 0;
	}
}

.mission__grid {
	display               : grid;
	grid-row-gap          : 46px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.mission__title {
	color         : $deep-teal;
	grid-column   : 4 span;
	padding-right : 20px;
	text-align    : center;

	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/5;
		padding-right : 20px;
		text-align    : start;
	}
}

.mission__text {
	color       : $deep-teal;
	grid-column : 4 span;
	text-align  : center;

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/13;
		text-align  : unset;
	}
}
