.people {
	background-color : $deep-teal;
}

.people__wrapper {
	grid-row-gap : 80px;
	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 0;
	}
}

.people__title {
	color         : $white;
	margin-bottom : 43px;

	@media screen and (min-width : $break-desktop) {

	}
}

.people__text {
	color         : $white;
	margin-bottom : 0;


	@media screen and (min-width : $break-desktop) {
		max-width : 625px;
	}
}
