.more-articles {
	background-color : $light-nude;
	padding-bottom   : 109px;
}

.more-articles__title {
	color         : $deep-teal;
	font-family   : 'Branch';
	font-size     : 60px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 107.5%;
	margin-bottom : 75px;
	text-align    : center;
}

.more-articles__grid {
	display               : grid;
	grid-row-gap          : 86px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 70px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 48px 20px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 145px;
	}
}
