.contact-hero {
	background-color : $deep-teal;
	padding          : 219px 0 68px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 234px 0 96px 0;
	}
}

.contact-hero__grid {
	display               : grid;
	grid-row-gap          : 80px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.contact-hero__wrap {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}
}

.contact-hero__title {
	color         : $white;
	font-size     : 50px;
	font-weight   : 400;
	line-height   : 54px;
	margin-bottom : 20px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		text-align : start;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 100px;
		line-height   : 108px;
		margin-bottom : 80px;

	}
}

.contact-hero__links {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		align-items : flex-start;
	}

	@media screen and (min-width : $break-desktop) {

	}

	a {
		color       : $white;
		display     : block;
		font-size   : 20px;
		font-weight : 300;
		line-height : 141%;


		@media screen and (min-width : $break-desktop) {
			font-size   : 26px;
			line-height : 120%;
		}

		&:not(:last-child) {
			margin-bottom : 10px;
		}
	}
}

.contact-hero__form {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 8/13;
	}
}
