.select {
	position : relative;
	width    : 100px;

	&__head {
		align-items      : center;
		background-color : transparent;
		border-radius    : 10px;
		color            : $deep-teal;
		cursor           : pointer;
		display          : flex;
		font-size        : 18px;
		font-weight      : 250;
		height           : 44px;
		letter-spacing   : 0.095em;
		line-height      : 148%;
		padding          : 0;
		position         : relative;
		width            : 100%;

		&:after {
			background-image    : url("../images/icon/chevron-right.svg");
			background-position : center;
			background-repeat   : no-repeat;
			background-size     : contain;
			content             : "";
			height              : 14px;
			position            : absolute;
			right               : 10px;
			top                 : 50%;
			transform           : translateY(-50%);
			transition          : all .3s ease;
			width               : 9px;
			z-index             : 1;
		}

		&.active {
			&::after {
				transform : translateY(-50%) rotate(90deg);
			}
		}
	}

	&__text {
		color          : #125E5E;
		font-size      : 15px;
		font-weight    : 250;
		letter-spacing : 0.095em;
		line-height    : 148%;
	}

	&__list {
		background-color : $light-nude;
		border-radius    : 10px;
		box-shadow       : 0 13px 41px rgba(9, 60, 102, 0.12);
		display          : flex;
		flex-direction   : column;
		left             : 0;
		opacity          : 0;
		position         : absolute;
		right            : 0;
		top              : 40px;
		transform        : scaleY(0);
		transform-origin : 0 0;
		transition       : all .3s ease;
		z-index          : 2;

		&.active {
			opacity   : 1;
			transform : scaleY(1);
		}
	}

	&__item {
		align-items : center;
		cursor      : pointer;
		display     : flex;
		min-height  : 40px;
		padding     : 0 12px;

		&.active {
			color : #0A3333;
		}
	}

	&__img {
		border-radius : 50%;
		height        : 20px;
		margin-right  : 10px;
		overflow      : hidden;
		width         : 20px;

		img {
			height     : 100%;
			object-fit : cover;
		}
	}
}
