.values {
	background-color : $light-nude;
	padding          : 80px 0 70px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 99px 0 92px 0;
	}
}

.values__wrapper {
	margin-bottom : 60px;
	text-align    : center;
}

.values__title {
	color         : $deep-teal;
	margin-bottom : 27px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
	}
}

.values__text {
	color         : $deep-teal;
	margin-bottom : 35px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 44px;
	}
}

.values__grid {
	display               : grid;
	grid-column-gap       : 20px;
	grid-row-gap          : 40px;
	grid-template-columns : repeat(6, 1fr);

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 0;
		grid-template-columns : repeat(12, 1fr);
	}
}

.values-item {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	grid-column    : 3 span;
	text-align     : center;
}

.values-item__icon {
	margin-bottom : 20px;
}

.values-item__text {
	color          : $deep-teal;
	font-family    : 'Branch';
	font-size      : 26px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 107.5%;
	margin-top     : auto;
	max-width      : 155px;
	text-align     : center;
	text-transform : capitalize;

	@media screen and (min-width : $break-desktop) {
		font-size : 40px;
		max-width : 253px;
	}

}
